import Vue from 'vue'
import Router from "vue-router";
import item from "./ask.vue";
import details from './details.vue';

Vue.use(Router)

const routes = [{
  path: '/',
  component: item
}, {
  path: '/item/:id',
  component: item
}, {
  path: '/details/:id/:details',
  component: details
}]




export default new Router({
  routes
})



