<template>
  <div id="app">
    <div class="header">
      <mt-header fixed>
        <a :href="'#/item/'+$route.params.id" slot="left">
          <mt-button icon="back"></mt-button>
        </a>
      </mt-header>
    </div>
    <mt-navbar v-model="active">
      <mt-tab-item id="ask" href='#/item/ask'>ask</mt-tab-item>
      <mt-tab-item id="share" href='#/item/share'>share</mt-tab-item>
      <mt-tab-item id="job" href='#/item/job'>job</mt-tab-item>
      <mt-tab-item id="good" href='#/item/good'>good</mt-tab-item>
    </mt-navbar>
    <div class="A-content">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
export default {
  name: "app",
  data() {
    return {
      active: "ask"
    };
  },
  created() {}
};
</script>

<style>
* {
  padding: 0;
  margin: 0;
}
.header {
  padding-top: 40px;
}
.clear:after {
  display: block;
  content: "";
  clear: both;
}
</style>



