<template>
  <div>
    <div v-for="(item,index) in date.data.data" class="item clear">
      <a :href="'#/details/'+active+'/'+item.id">
        <div class="i-image">
          <img :src="item.author.avatar_url">
        </div>
        <div class="i_text">
          <div class="i_title">
            {{item.title.length>10?item.title.slice(0,10):item.title}}
          </div>
          <div class="i_name">
            {{item.author.loginname}}
          </div>
          <p class="i_time">
            {{changeTime(item.last_reply_at)}}
          </p>
        </div>
      </a>
    </div>
  </div>
</template>

<script>
export default {
  props: [],
  name: "",
  data() {
    return {
      active: "",
      date: {}
    };
  },
  components: {},
  mounted() {},
  methods: {
    changeTime(date) {
      var newDate = new Date(date).toJSON();
      return new Date(+new Date(newDate) + 8 * 3600 * 1000)
        .toISOString()
        .replace(/T/g, " ")
        .replace(/\.[\d]{3}Z/, "");
    }
  },
  created() {},
  mounted() {
    this.active = this.$route.params.id;
    this.$http
      .get("https://cnodejs.org/api/v1/topics", {
        params: {
          page: 1,
          tab: this.active,
          limit: 20,
          mbrender: true
        }
      })
      .then(data => {
        this.date = data;
      });
  },
  watch: {
    $route() {
      this.active = this.$route.params.id;
      this.$http
        .get("https://cnodejs.org/api/v1/topics", {
          params: {
            page: 1,
            tab: this.active,
            limit: 10,
            mbrender: true
          }
        })
        .then(data => {
          console.log(data);
          this.date = data;
        });
    }
  }
};
</script>

<style>
.i-image {
  display: inline-block;
  width: 20%;
  min-width: 80px;
  min-height: 80px;
  margin-right: 0.3rem;
}
.i-image img {
  width: 100%;
  height: 100%;
}
.item {
  margin-bottom: 0.3rem;
}
.i_text {
  width: 60%;
  display: inline-block;
}
.i_title {
  font-size: 14px;
  color: #000000;
}
.i_name {
  color: #000000;
}
.item a {
  display: block;
  color: #000000;
}
.i_time{
	color: #000000;
}
</style>


