import Vue from 'vue';
import MintUI from 'mint-ui';
import 'mint-ui/lib/style.css';
import Resource from 'vue-resource';
import router from './route.js'
import App from './App.vue'

Vue.use(MintUI)
Vue.use(Resource)

new Vue({
  el: '#app',
  render: h => h(App),
  router
})



