<template>
  <div>
    <div v-for="(item,index) in date.data.data" v-if="item.id==id">
      <div v-html="item.content">
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: [],
  name: "",
  data() {
    return {
      date: {},
      active: this.$route.params.id,
      id: this.$route.params.details
    };
  },
  components: {},
  mounted() {
    this.$http
      .get("https://cnodejs.org/api/v1/topics", {
        params: {
          page: 1,
          tab: this.active,
          limit: 10,
          mbrender: true
        }
      })
      .then(data => {
        console.log(data);
        this.date = data;
      });
  },
  methods: {}
};
</script>

<style>
</style>


